import { React } from 'react';
import { PieChart } from 'react-minimal-pie-chart';

import './WinVsLossPieChart.scss';

export const WinVsLossPieChart = ({totalMatches, wins, losses}) => {
    const winPercentage = ((wins*100) / totalMatches).toFixed();
    const lossPercentage = ((losses*100) / totalMatches).toFixed();
    return (
        <div className="WinVsLossPieChart">
            <p className="WinLoss-Title">Wins / Losses</p>
            <PieChart
                data={[
                    { title: lossPercentage + '% Loss', value: losses, color: '#d17a77' },
                    { title: winPercentage + '% Win', value: wins, color: '#77d18c' },
                ]}
            />
        </div>
    );
}
