import { React } from 'react';
import { Link } from 'react-router-dom';

import './MatchDetailCard.scss';

export const MatchDetailCard = ({teamName, match}) => {
    if(!match) return null;
    const otherTeam = teamName === match.team1 ? match.team2 : match.team1;
    const otherTeamRoute = `/teams/${otherTeam}`;
    return (
        <div className={teamName === match.matchWinner ? 'MatchDetailCard Win' : 'MatchDetailCard Loss'}>
            <div className="BasicDetail">
                <span className="Vs">vs</span>
                <h1><Link to={otherTeamRoute}>{otherTeam}</Link></h1>
                <h2 className="MatchDate">{match.date}</h2>
                <h3 className="MatchVenu">at {match.venue}</h3>
                <h3 className="MatchResult">{match.matchWinner} won by {match.resultMargin} {match.result}</h3>
            </div>
            <div className="AdditionalDetail">
                <h3>First Inning</h3>
                <p>{match.team1}</p>
                <h3>Second Inning</h3>
                <p>{match.team2}</p>
                <h3>Man of the match</h3>
                <p>{match.playerOfMatch}</p>
                <h3>Umpires</h3>
                <p><span>{match.umpire1}</span>, <span>{match.umpire2}</span></p>
            </div>
        </div>
    );
};