import { React } from 'react';
import { Link } from 'react-router-dom';

import './Teams.scss';

export const Teams = ({teams}) => {
    return (
        <ul className="Teams">
            {
                teams.map(team => <li className="Team" key={team.id}><Link to={`/teams/${team.name}`}>{team.name}</Link></li>)
            }
        </ul>
    );
}