import { React } from 'react';
import { Link } from 'react-router-dom';

import './MatchSmallCard.scss';

export const MatchSmallCard = ({teamName, match}) => {
    if(!match) return null;
    const otherTeam = teamName === match.team1 ? match.team2 : match.team1;
    const otherTeamRoute = `/teams/${otherTeam}`;
    return (
        <div className={teamName === match.matchWinner ? 'MatchSmallCard Win' : 'MatchSmallCard Loss'}>
            <div>
                <span>vs</span>
                <h2><Link to={otherTeamRoute}>{otherTeam}</Link></h2>
            </div>
            <p className="MatchWinner">{match.matchWinner} won by {match.resultMargin} {match.result}</p>
        </div>
    );
}