import { React, useEffect , useState} from 'react';
import { Link, useParams } from 'react-router-dom';
import { MatchDetailCard } from '../components/MatchDetailCard';
import { MatchSmallCard } from '../components/MatchSmallCard';
import { WinVsLossPieChart } from '../components/WinVsLossPieChart';

import './TeamPage.scss';

export const TeamPage = () => {

    const [team, setTeam] = useState({matches: []});

    const {teamName} = useParams();

    const endYear = process.env.REACT_APP_DATE_END_YEAR;

    useEffect(
        () => {
            const fetchMatches = async () => {
                const response = await fetch(`http://localhost:8081/team/${teamName}`);
                const data = await response.json();
                setTeam(data);
            };
            fetchMatches();
        }, [teamName] // To skip continue calling, It specifies that it will call when component load for first time
    );
    if(!team || !team.name) {
        return <h1>Sorry, team not found.</h1>
    }
    return (
        <div className="TeamPage">
            <div className="TeamName-Section"><h1 className="TeamName">{team.name}</h1></div>
            <div className="Win-Loss-Section">
                <WinVsLossPieChart 
                    totalMatches={team.totalMatches} 
                    wins={team.totalWins} 
                    losses={team.totalMatches - team.totalWins}/>
            </div>
            <div className="MatchDetailCard-Section">
                <h3>Latest Matches</h3>
                <MatchDetailCard teamName={team.name} match={team.matches[0]}/>
            </div>
            {
                team.matches.slice(1).map(match => <MatchSmallCard teamName={team.name} match={match} key={match.id}/>)
            }
            <div className="More-Section">
                <Link to={`/teams/${team.name}/matches/${endYear}`}>More &#8594;</Link>  
            </div>
        </div>
    );
}