import { React, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { MatchDetailCard } from '../components/MatchDetailCard';
import { YearSelector } from '../components/YearSelector';

import './MatchPage.scss';

export const MatchPage = () => {
    const [matches, setMatches] = useState([]);
    const {teamName, year} = useParams();
    useEffect(
        () => {
            const fetchMatches = async () => {
                const response = await fetch(`http://localhost:8081/team/${teamName}/matches?year=${year}`);
                const data = await response.json();
                setMatches(data);
            }
            fetchMatches();
        }, [year, teamName]
    );

    return (
        <div className="MatchPage">
            <div className="YearSelector-Section">
                <h3>Select Year</h3>
                <YearSelector teamName={teamName} />
            </div>
            <div className="Matches">
                <h1 className="Matches-Heading">{teamName} matches in {year}</h1>
                { matches.map(match => <MatchDetailCard teamName={teamName} match={match} key={match.id}/>) }
            </div>
        </div>
    );
}